*{
    font-family: "Roboto", sans-serif;
    user-select: none;
}
html {
    scroll-behavior: smooth;
}
body {
    margin: 0;
    padding: 0;
}

  
.container{
    width: 1000px;
    margin: 0 auto;
}

@media (max-width: 700px) {
    .container{
        width: 90%;
        margin: 0 auto;
    }
}