.cart-item-block{
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    padding-bottom: 20px;
    position: relative;
    margin-top: 20px;
}

.cart-item-description{
    width: 70%;
}

@media (max-width: 768px) {
    .cart-item-description{
        margin-left: 20px;
        font-size: 12px;
    }
}